.tag-cloud {
  display: inline-block;
  width: 100%;
  margin-top: 20px;

  &__container {
    @include divider-combo-1;
    background-color: $sunny-yellow;
    padding: 60px 1em 0 1em;
    margin-top: 40px;
  }

  &__block {
    width: 100%;
    text-align: center;
    font-size: 13px;
    margin: 20px 0 0;
    padding: 0 0 20px;
    list-style-type: none;
  }

  &__icon {
    background: url('/static/img/tag-cloud-love-hands.svg') no-repeat 0 0/contain;
    padding: 2px 5px;
    height: 88px;
    width: 100px;
    position: absolute;
    left: 50%;
    margin-left: -50px;
  }

  &__heading {
    @include zig-zag-underline;
    text-align: center;

    h4 {
      font-family: $baton-medium;
      font-size: $font-x-large;
      text-transform: uppercase;
      color: $gunmetal;
    }
  }

  &__item {
    display: inline-block;
    padding: 2px 5px;
  }
}

//Some of the classes should be independent of the contianer div for elemnts reuse
.tag-cloud__anchor {
  font-family: $baton-medium;
  text-transform: lowercase;
  text-decoration: none;
  font-size: $font-large;
  color: $springster-purple;
  &:hover {
    color: $black;
  }
  &:before {
    content: "#";
    margin-right: -3px;
  }
}

//Tag Cloud for Article Page
.article-page-tag-cloud {
  text-align: center;
}
.article-page-tag-cloud__title {
  text-transform: uppercase;
}
.teaser-stacklist-tags {
  margin: 0;
  padding: 0;
}
.teaser-stacklist-tags__item {
  display: inline-block;
  padding: 2px 5px;
}
